/*
helper to reset default browser's styles from headings
*/

.r-title {
    margin-top: 0 !important;
    margin-top: var(--rTitleMarginTop, 0) !important;
    margin-bottom: 0 !important;
    margin-bottom: var(--rTitleMarginBottom, 0) !important;
}

/*
helper to hide elements that are available only for screen readers.
*/

.screen-reader {
    width: 1px !important;
    width: var(--screenReaderWidth, 1px) !important;
    height: 1px !important;
    height: var(--screenReaderHeight, 1px) !important;
    padding: 0 !important;
    padding: var(--screenReaderPadding, 0) !important;
    border: none !important;
    border: var(--screenReaderBorder, none) !important;

    position: absolute !important;

    position: var(--screenReaderPosition, absolute) !important;
    clip: rect(1px, 1px, 1px, 1px) !important;
    clip: var(--screenReaderClip, rect(1px, 1px, 1px, 1px)) !important;
    overflow: hidden !important;
    overflow: var(--screenReaderOverflow, hidden) !important;
}

/*
=====
POPUP
=====
*/

/*
1. Creating the top and bottom gaps for the modal so that content doesn't touches window's edges
*/

.popup:not(:target) {
    display: none;
}

.popup:target {
    width: 100%;
    height: 100vh;
    z-index: 1000;
    display: flex;

    position: fixed;
    top: 0;
    right: 0;
}

.popup::before {
    content: '';
    box-sizing: border-box;
    width: 100%;
    background-color: #ffffff;

    position: fixed;
    left: 0;
    top: 50%;
}

.popup::after {
    content: '';
    width: 0;
    height: 2px;
    background-color: #cbcbcb;

    position: absolute;
    top: 50%;
    left: 0;
    margin-top: -1px;
}

.popup__container {
    box-sizing: border-box;
    padding: 5% 15%;

    height: calc(100vh - 40px); /* 1 */
    margin: auto; /* 1 */
    overflow: auto; /* 1 */
    -ms-scroll-chaining: none;
        overscroll-behavior: contain; /* 1 */
}

.popup__title {
    --rTitleMarginBottom: 1.5rem;
    font-size: 1.5rem;
}

.popup__close {
    width: 2rem;
    height: 2rem;

    position: fixed;
    top: 1.5rem;
    right: 1.5rem;

    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    background-image: url(data:image/svg+xml;base64,);
}

/*
animation
*/

.popup::before {
    will-change: height, top;
    -webkit-animation: open-animation 0.6s cubic-bezier(0.83, 0.04, 0, 1.16) 0.65s both;
            animation: open-animation 0.6s cubic-bezier(0.83, 0.04, 0, 1.16) 0.65s both;
}

.popup::after {
    will-change: width, opacity;
    -webkit-animation: line-animation 0.6s cubic-bezier(0.83, 0.04, 0, 1.16) both;
            animation: line-animation 0.6s cubic-bezier(0.83, 0.04, 0, 1.16) both;
}

@-webkit-keyframes line-animation {
    0% {
        width: 0;
        opacity: 1;
    }

    99% {
        width: 100%;
        opacity: 1;
    }

    100% {
        width: 100%;
        opacity: 0;
    }
}

@keyframes line-animation {
    0% {
        width: 0;
        opacity: 1;
    }

    99% {
        width: 100%;
        opacity: 1;
    }

    100% {
        width: 100%;
        opacity: 0;
    }
}

@-webkit-keyframes open-animation {
    0% {
        height: 0;
        top: 50%;
    }

    100% {
        height: 100vh;
        top: 0;
    }
}

@keyframes open-animation {
    0% {
        height: 0;
        top: 50%;
    }

    100% {
        height: 100vh;
        top: 0;
    }
}

.popup__container {
    -webkit-animation: fade 0.5s ease-out 1.3s both;
            animation: fade 0.5s ease-out 1.3s both;
}

@-webkit-keyframes fade {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes fade {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}
@media (max-width: 426px) {
    .popup__container {
        padding: 5% 5%;
    }
}
